<template>
  <div>
    <div v-if="!item.self" class="flex">
      <div class="fx-pc" style="width: 50px;">
        <span v-if="!headImgMap[item.clientName]" class="fz30 fa fa-circle"></span>
        <img v-if="headImgMap[item.clientName]" :src="headImgMap[item.clientName]" alt="" class="headImg">
      </div>
      <div class="fx-ver" style="width: 100%">
        <div style="height: 20px;">
          <span class="fz12">{{ nickNameMap[item.clientName] || item.clientName }}</span>
        </div>
        <div>
          <div class="msg"><span style="white-space:pre-wrap;">{{ item.msg }}</span></div>
        </div>
      </div>
    </div>
    <div v-if="item.self" class="flex">
      <div class="msgBox fx-pe fx-ac">
        <div class="msg fr"><span style="white-space:pre-wrap;">{{ item.msg }}</span></div>
      </div>
      <div class="fx-pc" style="width: 50px;" @click="openDialog">
        <span v-if="!headImgMap[item.clientName]" class="fz30 fa fa-circle"></span>
        <img v-if="headImgMap[item.clientName]" :src="headImgMap[item.clientName]" alt="" class="headImg">
      </div>
    </div>
  </div>
</template>

<script>
import client from "@/utils/ConnectWebSocket";

export default {
  name: "Message",
  props: ['item'],
  data() {
    return {
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
    }
  },
  methods: {
    openDialog() {
      this.$parent['openDialog']('headImg');
    }
  }
}
</script>

<style scoped>
@import "../assets/chatroom.css";
</style>