<template>
  <div class="mainBox flex">
    <div class="left">
      <div class="fx-ac">
        <button @click="connect">连接</button>
        <button @click="exit">断开</button>
        <div :class="self.status === 'onLine'?'onLine':'offline'"></div>
        <span class="name"
              @click="openDialog('nickname')">{{ nickNameMap[self.clientName] || self.clientName }}</span>
      </div>
      <div class="member">在线列表👇：</div>
      <div class="fx-ver memberList">
        <div class="member" v-for="(item,idx) in memberList.list" :key="idx">
          <span>{{ nickNameMap[item] || item }}</span>
        </div>
      </div>
    </div>
    <div class="right fx-ver">
      <div class="chatName">
        <div class="groupHImg fx-ac">
          <span v-if="!groupImg" class="fa fa-magnet fz24" @click="openDialog('groupImg')"></span>
          <img v-if="groupImg"
               :src="groupImg"
               alt=""
               class="headImg"
               @click="openDialog('groupImg')"/>
          <span class=" fz22">大型多人激情聊天现场</span>
        </div>
      </div>
      <div class="top" id="talkBox">
        <div class="oneLine" v-for="(item,idx) in messageList" :key="idx">
          <Message :item=item></Message>
        </div>
      </div>
      <div class="down">
        <textarea v-model="text" style="width: 99%;height: 97%" @keyup.enter="sendMsg">
        </textarea>
      </div>
    </div>
    <el-dialog
        title="提示"
        :visible.sync="showBox"
        width="20%"
        :modal="false">
      <label v-if="type==='nickname'">
        <span>输入昵称：</span>
        <input type="text" v-model="msg" maxlength="20" @keyup.enter="setNickname" class="input"/>
      </label>
      <label v-if="type.endsWith('Img')">
        <div>输入在线图片地址：</div>
        <input type="text" v-model="msg" @keyup.enter="setHeadImg" class="input"/>
      </label>
      <span slot="footer" class="dialog-footer">
    <el-button @click="showBox = false">取 消</el-button>
    <el-button type="primary" @click="submit">确 定</el-button>
  </span>
    </el-dialog>
  </div>

</template>

<script>
import client from '../utils/ConnectWebSocket'
import Message from './Message'

export default {
  name: "ChatRoom",
  components: {Message},
  data() {
    return {
      messageList: client.getMessageList(),
      self: client.getSelf(),
      memberList: client.getMemberList(),
      nickNameMap: client.getNickNameMap(),
      headImgMap: client.getHeadImgMap(),
      text: "",
      talkBox: null,
      showBox: false,
      msg: '',
      type: '',
      groupImg: null,
    }
  },
  created() {
    global.view = this;
  },
  mounted() {
    this.talkBox = document.getElementById('talkBox');
  },
  methods: {
    exit() {
      if (this.self.status === 'offline') {
        this.$message.error("未登录！");
        return;
      }
      client.close();
    },
    connect() {
      if (this.self.status === 'onLine') {
        this.$message.error("已登录！");
        return;
      }
      this.messageList.length = 0;
      client.getConnection("chatroom");
      client.on("data", this.onRecvData)
    },
    sendMsg() {
      let Msg = {
        msg: this.text,
        sendTime: Date.now()
      }
      this.text = '';
      client.writeData(JSON.stringify(Msg));
    },
    setNickname() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: 'nickname'
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.showBox = false;
    },
    onRecvData(event) {
      let data = event.data;
      let Msg = JSON.parse(data);
      switch (Msg.type) {
        case "talk": {
          Msg.self = Msg.clientName === this.self.clientName;
          this.messageList.push(Msg);
          break;
        }
        case "auth": {
          client.setSelf(Msg.clientName);
          break;
        }
        case "upLine": {
          this.memberList.list.push(Msg.clientName);
          this.memberList.list = [...this.memberList.list];
          break;
        }
        case "offLine": {
          client.oneOffLine(Msg.clientName);
          break;
        }
        case "nickname": {
          this.$set(this.nickNameMap, Msg.clientName, Msg.msg);
          break;
        }
        case 'headImg': {
          this.$set(this.headImgMap, Msg.clientName, Msg.msg);
          break;
        }
        case 'groupImg': {
          this.groupImg = Msg.msg;
        }
      }
      setTimeout(() => {
        this.talkBox.scrollTop = this.talkBox.scrollHeight;
      }, 250);
    },
    openDialog(type) {
      this.type = type;
      this.showBox = true;
    },
    setHeadImg() {
      let Msg = {
        msg: this.msg,
        sendTime: Date.now(),
        type: this.type
      }
      this.msg = '';
      client.writeData(JSON.stringify(Msg));
      this.showBox = false;
    },
    submit() {
      if (this.type === 'nickname') this.setNickname();
      if (this.type === 'headImg' || this.type === 'groupImg') this.setHeadImg();
    }
  }
}
</script>

<style scoped>
@import "../assets/chatroom.css";
</style>